import React, { Component } from 'react'
import ErrorFound from '../../../../ErrorFound'
import InheritSwitch from './Element/InheritSwitch'
import { Form } from 'react-bootstrap'
import ColorPicker from '../../CommonComponents/ColorPicker'
import SelectFont from '../Components/SelectFont'
import { connect } from 'react-redux'
import { getFeedlist } from '../../../../../actions/appsMoreAction'
import Select from "react-select";
import AttachImage from '../../CommonComponents/Uploads/AttachImage'
import { CHANGE_BANNER } from '../../../../../reducer/keys'

class SocialHeader extends Component {

    state={
        networkList:[{label:"All", value:0}],
        selectedNetwork: {lable:"All", value: 0},
    }

    componentDidMount(){
        const { wallData } = this.props
        getFeedlist(wallData?.ThemeInfo?.wallId || localStorage.getItem('active_wall')).then(response=>{
            const { responsedata } = response.data
            const list = responsedata?.feeds?.map(item=>({ label : item.networkName, value : item.networkId }))
            this.setState(prevState => ({networkList: [...prevState.networkList, ...list]}))
        })
    }

    updateSocialBanner = (key, key2, key3, value) => {
        const { wallData, changeBanner } = this.props
        const { selectedNetwork } = this.state
        const isGlobalUpdate = [0, 'font', 'color'].includes(key3);

        const updatedData = wallData?.Banner?.map(item => isGlobalUpdate ? { ...item, [key]: {...item[key], [key2]: key3 ? {...item[key][key2], [key3]: value} : value }} : item?.network?.id === selectedNetwork.value ? { ...item, [key]: {...item[key], [key2]: key3 ? {...item[key][key2], [key3]: value} : value }, userUpdated: 1 } : item
        );
        console.log(updatedData,"updatedData", wallData?.Banner)
        changeBanner(updatedData)
    }

    render() {
        const { inheritStyle, wallData } = this.props;
        const { Reviews, Banner } = wallData
        const { networkList, selectedNetwork } = this.state
        
        const data = Banner?.find(item=> item.network.id == selectedNetwork.value)
        console.log(data,"Data000--->34" );
        
        
        return <>
            <ErrorFound><InheritSwitch/></ErrorFound>
            <div>
                <p className="h5">Overall settings</p>
            </div>
            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bg_c">Background color</Form.Label>
                <ColorPicker id="s_bg_c" isDisabled={""} defaultColor={data?.background?.primaryColor} changeColor={(clr) => this.updateSocialBanner('background', 'primaryColor', 0, clr)} />
            </div>

            <div className="mt-4">
                <SelectFont inheritStyle={inheritStyle} fontChange={(font, variant) => this.updateSocialBanner('title', 'style', 'font', font.label.key)} variantChange={this.onChangeVariant} fontval={data?.title.style.font} vrntVal={data?.title.style.font} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_f_c">Font color</Form.Label>
                <ColorPicker id="s_f_c" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={data?.title.style.color} changeColor={(clr) => this.updateSocialBanner('title', 'style', 'color', clr)} inheritStyle={inheritStyle} />
            </div>

            <hr className=""></hr>
            <div>
                <p className="h5">Other settings</p>
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor={`s_ntwrk`}>Select network</Form.Label>
                <Select id="s_ntwrk" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={networkList}
                value={networkList[networkList.findIndex(i => i.value == selectedNetwork.value)]}
                onChange={e => this.setState({selectedNetwork: e})} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_t">Title</Form.Label>
                <Form.Control id="s_t" value={data?.title?.text} maxLength="30" onChange={e => this.updateSocialBanner('title', 'text', null, e.target.value)} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_st">Subtitle</Form.Label>
                <Form.Control id="s_st" value={data?.subTitle?.text} maxLength="30" onChange={e => this.updateSocialBanner('subTitle', 'text', null, e.target.value)} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_pi">Profile image</Form.Label>
                <AttachImage id="s_pi" imageStyle={{ paddingBottom: '66%' }} backgroundImage={data?.logo?.url} onClickTouploadImage={(url)=> this.updateSocialBanner('logo', 'url', null, url)} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bt">Button text</Form.Label>
                <Form.Control id="s_bt" value={data?.subTitle?.style?.font} maxLength="30" onChange={e => this.updateSocialBanner('subTitle','style','font', e.target.value)} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bt_txt_c">Button text color</Form.Label>
                <ColorPicker id="s_bt_txt_c" isDisabled={""} defaultColor={data?.background?.secondaryColor} changeColor={(clr) => this.updateSocialBanner('background','secondaryColor', null, clr)} />
            </div>

            <div className="mt-4">
                <Form.Label className="mb-2" htmlFor="s_bt_c">Button color</Form.Label>
                <ColorPicker id="s_bt_c" isDisabled={""} defaultColor={data?.social?.style?.color} changeColor={(clr) => this.updateSocialBanner('social','style','color', clr)} />
            </div>
        
        </>
    }
}

const mapStateToProps = (state) => {
    return {
        allFont: state.allFont?.allFonts,
        isHeaderUpdate: state.appData?.isHeaderUpdate,
        inheritStyle: state.appData?.wall?.CardStyle?.inheritStyles,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        changeBanner: (data) => dispatch({type:CHANGE_BANNER, payload:data})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SocialHeader)
