import React, { Component } from 'react'
import { connect } from 'react-redux'
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper'
import { Button, Row, Col, Image, Tab, Form, Nav } from 'react-bootstrap'
import { APPLY_THEME_ACTION, CHANGE_THEME_ACTION } from '../../../../../actions/themeAction'
import { checkPermission } from '../../../../../actions/collaboratorActions'
import RequestButtonHOC from '../../CommonComponents/RequestButtonHOC'
import { CHANGE_BANNER_STATUS, CHANGE_HEADER_LAYOUT, TOGGLE_LAYOUT } from '../../../../../reducer/keys'

export const ThemeItem = ({ Index, dataTheme, action, active, themeName, header }) => {
    return (<Col key={Index} className="col-4 p-0 select-grid">
        <Button data-theme={dataTheme} variant="grid" className={`rounded-0 d-flex align-items-center flex-column py-4 px-0 border-0 w-100 ${dataTheme == active ? 'active' : ''}`} onClick={action}>
            <div className="w-50px h-50px mb-2 d-flex align-items-center">
                {!header ? <Image className="invert" loading='lazy' src={baseUrl(`media/icons/${header ? 'review-header' : 'theme'}/${dataTheme}.png`)} height={46} width={150} alt={themeName} title={themeName} fluid /> : <FontAwesomeIcon icon={dataTheme == 6 ? 'star' : dataTheme == 7 ? 'bullhorn' : dataTheme == 8 ? 'stars' : 'cloud-arrow-up'} />}
            </div>
            {<p className="mb-0 text-center fs-9">{themeName}</p>}

        </Button>
    </Col>)
}

export const RequestButton = () => {
    return (<Col className="col-4 p-0 select-grid">
    <RequestButtonHOC>
        <Button variant="grid" className={`rounded-0 d-flex align-items-center flex-column py-4 px-0 border-0 w-100`}>
            <div className="w-50px h-30px mb-2 f-center">
                <FontAwesomeIcon type={'light'} icon={'plus-circle'} iconClass={'fs-1 me-0 text-gray-400'}/>
            </div>
            <p className="mb-0 text-center">Request New Themes</p>
        </Button>
    </RequestButtonHOC>
</Col>)
}

const bannerName = {
    6:"Review",
    7:"Social",
    8:"Star Review",
    0:"Custom",
    4:"Old Banner",
}

class ThemeList extends Component {

    state = (() => {
        const wall = this.props.appData?.wall || {}; // Store wall in a variable
        const { ThemeInfo = {}, Reviews = {} } = wall; // Destructuring for cleaner access

        return {
          selectedtheme: false,
          themeId: ThemeInfo?.id,
          isHeader: Reviews?.headerStatus,
          isWidget: Reviews?.themeStatus ?? false, // Default to false if key is missing
          headerId: Reviews?.headerType,

          bnrStatus: null, // New status show banner active 
        };
      })();

    onClose = () => this.setState({ selectedtheme: false, themeId: null })

    componentDidMount() {
         this.setState({ themeId: this.props.appData?.wall?.ThemeInfo?.id })
    }
    
    changeThemes = (themeID, type, wallId) => {  
        this.setState({ selectedtheme: true, themeId: themeID })
        this.props.CHANGE_THEMES(themeID, type, wallId);
    }

    enableLayout = (type, val) => {
        const stateType = type == 'header' ? 'isHeader' : 'isWidget'
        this.setState({[stateType] : Number(val) }, ()=>this.props.toggleLayout({header:this.state.isHeader, widget:this.state.isWidget}))
    }

    changeHeader = (id) => { if(id != this.state.bnrStatus) this.setState({ bnrStatus:id }, ()=> this.props.changeBannerStatus(id))}

    componentDidUpdate(prevProps) {

        const {appData, sectionChanged, socialReview, selectedContent, resetSection} = this.props

        if (appData.save_and_publish && !prevProps.appData.save_and_publish && appData.is_themes_change) {
            this.props.APPLY_THEME(this.state.themeId);
        }

       if (appData?.wall?.ThemeInfo?.id !== prevProps.appData?.wall?.ThemeInfo?.id) {
            this.setState({ themeId: appData?.wall?.ThemeInfo?.id ,headerId:appData?.wall?.Reviews?.headerType, isHeader: appData?.wall?.Reviews?.headerStatus, isWidget:appData?.wall?.Reviews?.themeStatus})
        }

        if(appData?.bannerData?.length !== prevProps.appData?.bannerData?.length){
            const res = appData?.bannerData.filter((item) => item.customBannerStatus)
            this.props.changeBannerStatus(res?.[0]?.bannerType)
        }

        // if(prevProps.sectionChanged == false && sectionChanged == true){
        //     this.changeThemes(Number(socialReview) == 1 ? 62 : 63, Number(socialReview), selectedContent?.id)
        //     resetSection()
        // }
    }


    render() {
        const { themeList, socialReview, selectedContent, changeThemeSection, bannerData } = this.props
        const { themeId, isHeader, isWidget, headerId, bnrStatus } = this.state
        const {themesList} = themeList

        const chooseBanner = bannerData?.filter((item, index, self) => index === self.findIndex((t) => t.type === item.type));      

        return (<div className='px-4 pb-3 mb-4'>
            {themesList ?
            <>
                <Nav variant="pills" className="f-center my-2" activeKey={socialReview} onSelect={event => changeThemeSection(event)}>
                    <Nav.Item>
                        <Nav.Link className="border btn btn-primary mx-1 py-2" eventKey="1">Social</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="position-relative">
                        <Nav.Link className="border btn btn-primary mx-1 py-2" eventKey="2">Reviews</Nav.Link>
                        {/* <span className="position-absolute badge-primary fs-10 border border-2 border-light badge m-0 badge" style={{ top: '-7px', right: '-14px', padding: '2px' }}>New</span> */}
                    </Nav.Item>
                </Nav>

                <Row className="select-group flex-wrap row gx-8 pb-5">
                    <div className="f-between w-100 border-bottom">
                        <Form.Label className="my-4" htmlFor="hlayout_">
                            <span className="me-2 fs-9">BANNER LAYOUT</span>
                        </Form.Label>
                        <Form.Check type="switch" id="hlayout_" checked={isHeader} onChange={event=> checkPermission('publish', 'website',true) ? this.enableLayout('header',event.target.checked) : {}}/>
                    </div>
                    {chooseBanner?.map((Item, Index) => (
                        (socialReview == '1' && [0,2,3,4,7].includes(Item.type) || socialReview == '2' && [6, 8].includes(Item.type)) ? <ThemeItem key={Index} Index={Index} dataTheme={Item.type} active={Item.status ? Item.type : null} themeName={bannerName[Item.type]} action={() => checkPermission('publish', 'website',true) ? this.changeHeader(Item.type) : {}} header={true}/> : null
                    ))}
                </Row>

                <Row className="select-group flex-wrap row gx-8 border-top">
                    <div className="f-between w-100 border-bottom">
                        <Form.Label className="my-4" htmlFor="wlayout_">
                            <span className="me-2 fs-9">THEME LAYOUT</span>
                        </Form.Label>
                        {/* <Form.Check type="switch" id="wlayout_" checked={isWidget} onChange={event => checkPermission('publish', 'website',true) ? this.enableLayout('widget', event.target.checked) : {}} /> */}
                    </div>
                    {themesList.map((Item, Index) => ( 
                        (Item.themeType ==Number(socialReview) || Item.themeId == 5) && <ThemeItem key={Index} Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}}/>
                    ))}
                </Row>


                {/* <Tab.Container id="socialReviews" activeKey={socialReview}>
                    <Tab.Content>
                        <Tab.Pane key={1} eventKey="1">
                            <Row className="select-group flex-wrap row gx-8 border-top">
                                {themesList.map((Item, Index) => (
                                    Item.themeType !=2 && <ThemeItem key={Index} Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}}/>
                                ))}
                                <RequestButton />
                            </Row>
                        </Tab.Pane>
                        <Tab.Pane  key={2} eventKey="2">
                            <Row className="select-group flex-wrap row gx-8">
                                <div className="f-between w-100 border-bottom">
                                    <Form.Label className="my-4" htmlFor="hlayout_">
                                        <span className="me-2 fs-9">HEADER LAYOUT</span>
                                    </Form.Label>
                                    <Form.Check type="switch" id="hlayout_" checked={isHeader} onChange={event=> checkPermission('publish', 'website',true) ? this.enableLayout('header',event.target.checked) : {}}/>
                                </div>
                                {themeHeader.map((Item, Index) => ( 
                                    <ThemeItem key={Index} Index={Index} dataTheme={Item.id} active={headerId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeHeader(Item.id) : {}} header={true}/>
                                ))}
                            </Row>
                            <Row className="select-group flex-wrap row gx-8 border-top">
                                <div className="f-between w-100 border-bottom">
                                    <Form.Label className="my-4" htmlFor="wlayout_">
                                        <span className="me-2 fs-9">WIDGET LAYOUT</span>
                                    </Form.Label>
                                    <Form.Check type="switch" id="wlayout_" checked={isWidget} onChange={event => checkPermission('publish', 'website',true) ? this.enableLayout('widget', event.target.checked) : {}} />
                                </div>
                                {themesList.map((Item, Index) => ( 
                                    Item.themeType !=1 && <ThemeItem key={Index} Index={Index} dataTheme={Item.themeId} active={themeId} themeName={Item.name} action={() => checkPermission('publish', 'website',true) ? this.changeThemes(Item.themeId, Item.themeType, selectedContent?.id) : {}}/>
                                ))}
                            </Row>
                        </Tab.Pane>
                    </Tab.Content>
                
                </Tab.Container> */}
            </> : null }
           
        </div>
        )
    }
}

const mapStateToProps = (state) => { 
    return {
        appData: state.appData
    }
}
const mapDispatchToProps = dispatch => {
    return {
        CHANGE_THEMES: (id, type, wallKey) => dispatch(CHANGE_THEME_ACTION(id, type, wallKey)),
        APPLY_THEME: (id) => dispatch(APPLY_THEME_ACTION(id)),
        selectHeader: (id) => dispatch({type:CHANGE_HEADER_LAYOUT, payload:id}),
        toggleLayout: (data) => dispatch({type: TOGGLE_LAYOUT, payload:data}),
        changeBannerStatus: (id) => dispatch({type: CHANGE_BANNER_STATUS, payload:id})
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThemeList);